.about {
  &::before {
    background-image: linear-gradient(to top, rgb(0, 0, 0), #312727b0);
  }

  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 70px;
    flex-shrink: 0;
    z-index: 1;

    @include tablet {
      gap: 30px;
    }
  }

  &__inner {
    display: flex;
    gap: 50px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__title {
    flex-shrink: 0;
  }

  .achievements {
    &__slider {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 20px;
      width: 100%;
    }

    &__items {
      width: 100%;
    }

    &__item {
      position: absolute;
      top: 0;
      left: 0;

      width: 0;
      height: 0;

      opacity: 0;
      visibility: hidden;

      * {
        font-size: 0;
        width: 0;
        height: 0;
      }
    }

    &__pagination {
      display: flex;
    }
  }

  .ms--images {
    position: relative;
    overflow: hidden;
  }
  .ms--images.ms-container--horizontal {
    width: 100%;
    height: 500px;
    max-width: 100%;
    mask: linear-gradient(90deg, transparent, #fff 20%, #fff 80%, transparent);

    @include tablet {
      height: 300px;
    }
  }

  .ms--images.ms-container--horizontal .ms-slide {
    display: inline-flex;
  }
  .ms--images .ms-track {
    display: flex;
    position: absolute;
    white-space: nowrap;
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .ms--images .ms-slide {
    align-items: center;
    justify-content: center;
    width: 500px;
    height: 100%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    @include tablet {
      width: 300px;
    }
  }

  .ms--images {
    left: 0;
  }
  .ms--images.ms-container--horizontal .ms-track {
    left: 20px;
    height: 100%;
    gap: 30px;

    @include tablet {
      gap: 20px;
      left: 0;
    }
  }

  .ms--images .ms-slide__image-container {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    overflow: hidden;
  }
  .ms--images .ms-slide__image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .ms--numbers {
    position: relative;
    overflow: hidden;
  }
  .ms--numbers.ms-container--horizontal {
    width: 240px;
    height: 240px;
    max-width: 100%;

    @include tablet {
      width: 150px;
      height: 150px;
    }
  }
  .ms--numbers.ms-container--horizontal .ms-track {
    left: calc(50% - 120px);
  }
  .ms--numbers.ms-container--horizontal .ms-slide {
    display: inline-flex;
  }
  .ms--numbers .ms-track {
    display: flex;
    position: absolute;
    white-space: nowrap;
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .ms--numbers .ms-slide {
    align-items: center;
    justify-content: center;
    width: 240px;
    height: 240px;
    text-shadow: 5px 5px 5px black;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    overflow: hidden;

    @include tablet {
      height: 100px;
    }
  }

  .ms--numbers {
    position: absolute;
    left: calc(50% - 530px);
    top: -100px;
    pointer-events: none;

    @include tablet {
      top: 50px;
    }
  }
  .ms--numbers .ms-slide {
    font-size: 150px;
    font-weight: 700;
    color: $white;

    @include tablet {
      font-size: 100px;
    }
  }

  .ms--titles {
    position: relative;
    overflow: hidden;
  }
  .ms--titles.ms-container--vertical {
    width: 400px;
    height: fit-content;
    min-height: 200px;
    max-height: 200px;
  }
  .ms--titles.ms-container--vertical .ms-track {
    flex-direction: column;
    top: calc(50% - 85px);
  }
  .ms--titles.ms-container--vertical.ms-container--reverse .ms-track {
    flex-direction: column-reverse;
    top: auto;
    bottom: calc(50% - 85px);
  }
  .ms--titles.ms-container--vertical .ms-slide {
    display: flex;
  }
  .ms--titles .ms-track {
    display: flex;
    position: absolute;
    white-space: nowrap;
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .ms--titles .ms-slide {
    align-items: center;
    justify-content: center;
    width: 400px;
    height: 170px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    @include tablet {
      width: 300px;
    }
  }

  .ms--titles {
    position: absolute;
    left: calc(50% - 550px);
    z-index: 1;
    pointer-events: none;
  }
  .ms--titles .ms-track {
    white-space: normal;
  }
  .ms--titles .ms-slide {
    font-size: 3.3em;
    font-weight: 600;

    @include tablet {
      justify-content: flex-start;
    }
  }
  .ms--titles .ms-slide h3 {
    margin: 0;
    font-size: 40px;
    text-shadow: 5px 5px 5px black;

    @include tablet {
      font-size: 30px;
    }
  }

  .ms--links {
    position: relative;
    overflow: hidden;
  }
  .ms--links.ms-container--vertical {
    width: 120px;
    height: 60px;
    max-height: 100%;
  }
  .ms--links.ms-container--vertical .ms-track {
    flex-direction: column;
    top: calc(50% - 30px);
  }
  .ms--links.ms-container--vertical .ms-slide {
    display: flex;
  }
  .ms--links .ms-track {
    display: flex;
    position: absolute;
    white-space: nowrap;
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .ms--links .ms-slide {
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 60px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .ms--links {
    position: absolute;
    left: calc(50% - 420px);
    top: calc(50% + 105px);
    z-index: 1;
  }
  .ms--links .ms-track {
    white-space: normal;
  }
  .ms--links .ms-slide__link {
    font-weight: 600;
    padding: 5px 0 8px;
    border-bottom: 2px solid white;
    cursor: pointer;
  }

  .pagination {
    display: flex;
    position: absolute;
    left: calc(50% - 420px);
    top: calc(100%);
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    z-index: 1;
  }
  .pagination__button {
    display: inline-block;
    position: relative;
    width: 36px;
    margin: 0 5px;
    cursor: pointer;

    @include tablet {
      width: 27px;
    }
  }
  .pagination__button:before,
  .pagination__button:after {
    content: "";
    position: absolute;
    left: 0;
    top: calc(50% - 1px);
    width: 100%;
    box-shadow: 0 1px 0 #0b0d14;
  }
  .pagination__button:before {
    height: 3px;
    background-color: #6a3836;
  }
  .pagination__button:after {
    height: 3px;
    background-color: $red;
    opacity: 0;
    transition: 0.5s opacity;
  }

  .pagination__item--active .pagination__button:after {
    opacity: 1;
  }

  @include tablet {
    .ms--numbers {
      left: calc(50% - 150px);
    }

    .ms--titles {
      left: 0;
      top: 7%;
    }

    .ms--links {
      left: calc(50% - 60px);
      top: calc(50% + 80px);
    }

    .pagination {
      left: 50%;
      top: calc(100% - 50px);
      transform: translateX(-50%);
    }
  }
  @include tablet {
    .ms--images {
      overflow: visible;
    }
  }
  @include tablet {
    .ms--titles .ms-slide {
      transform: scale(0.8);
    }
  }
}
