h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  line-height: normal;
}

h1 {
  font-weight: 700;
  font-size: 64px;

  @include tablet {
    font-size: 32px;
  }
}

h2 {
  font-weight: 700;
  font-size: 40px;

  @include tablet {
    font-size: 24px;
  }
}

h3 {
  font-weight: 700;
  font-size: 34px;

  @include tablet {
    font-size: 20px;
  }
}

h4 {
  font-weight: 600;
  font-size: 24px;

  @include tablet {
    font-size: 18px;
  }
}

h5 {
  font-weight: 600;
  font-size: 20px;

  @include tablet {
    font-size: 18px;
  }
}

p {
  font-size: 20px;
  line-height: 150%;

  * {
    color: $red;
  }

  @include tablet {
    font-size: 14px;
  }
}

.main-title {
  font-size: 90px;
  line-height: 120%;
  text-transform: capitalize;
  text-shadow: 5px 5px 5px black;
  text-align: center;

  span {
    color: $red;
    font: inherit;
  }

  @include tablet {
    font-size: 34px;
    line-height: normal;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 15px;

  @include tablet {
    margin-bottom: 10px;
  }
}

p {
  &:not(:last-child) {
    margin-bottom: 15px;

    @include tablet {
      margin-bottom: 10px;
    }
  }

  + *:not(div, p, ul, span) {
    margin-top: 40px;

    @include tablet {
      margin-top: 30px;
    }
  }
}
